.font {
  &--xs {
    font-size: $font-xs;
  }

  &--sm {
    font-size: $font-sm;
  }

  &--md {
    font-size: $font-md;
  }

  &--lg {
    font-size: $font-lg;
  }

  &--xl {
    font-size: $font-xl;
  }

  &--bold {
    font-weight: bold;
  }

  &--italic {
    font-style: italic;
  }

  &__title {
    &--md {
      font-size: $font-title-md;
    }

    &--sm {
      font-size: $font-title-sm;
    }
  }

  &__brand {
    &--md {
      font-size: $font-brand-md;
    }

    &--sm {
      font-size: $font-brand-sm;
    }
  }
}