.company {
  &__header {
    margin: 30px 0;
  }
  
  &__info {
    @include wrapper(80%);

    @include media-screen($screen-md) {
      @include wrapper(90%);
    }
  }

  &__item {
    margin-bottom: 20px;
    color: $main-color-dark;
  }

  &__links {
    @include wrapper(80%);

    &--hidden {
      @include wrapper(80%);
      transform: scaleY(0);
      transform-origin: top center;
      height: 0;
      transition: 0.5s;
      overflow: hidden;
      margin-bottom: 30px;
      
      @import "company-links/company-links";
    }

    @import "company-links/company-links";
  }

  &__arrows {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 220px;
    overflow: hidden;
    position: relative;
    cursor: pointer;

    .arrow {
      font-size: 10rem;
      position: absolute;

      &:nth-child(1) {
        top: 0;
        opacity: 0.5;
      }

      &:nth-child(2) {
        top: 30px;
        opacity: 0.6;
      }

      &:nth-child(3) {
        top: 60px;
        opacity: 0.7;
      }
    }
  }

  @include media-screen(xl) {
    @include wrapper(50%);
  }

  @include media-screen(lg) {
    @include wrapper(70%);
  }

  @include media-screen(md) {
    @include wrapper(90%);
  }

  @include media-screen($screen-md) {
    margin: 0 10px;

    &__links {
      @include wrapper(90%);

      &--hidden {
        @include wrapper(90%);
      }
    }
  }
}