.info-table {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, 30px);
  grid-gap: 20px;
  justify-content: center;
  align-content: center;

  &__row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 50px;
    align-items: center;
    align-content: center;
    justify-content: center;
    justify-items: center;
    grid-gap: 20px;
  }

  &__header {
    text-align: end;
    color: $main-color-dark;
    
    @include media-screen($screen-md) {
      text-align: center;
    }
  }

  &__content {
    color: $main-color-dark;
  }
}