.article {
  @include wrapper(745px);

  &__pane {
    float: left;
    width: 350px;
    height: 250px;
    margin-right: 30px;
    overflow: hidden;
    @include display_flex_mixin(row, center, center);

    @include media-screen($screen-md) {
      float: none;
      width: 100%;
      text-align: center;
    }
  }

  &__image {
    height: 100%;

    @include media-screen($screen-sm) {
      width: 100%;
      height: 200px;
    }
  }

  &__content {
    text-align: justify;
    line-height: 2.4rem;
    width: 100%;
  }

  &__paragraph {
    margin-bottom: 15px;
  }

  &__item {
    margin-left: 40px;
    margin-bottom: 10px;
  }

  @include media-screen(744px) {
    width: 100%;
  }
}