.item-articles {
  @include display_grid_mixin(1fr 4fr, 1fr, 20px);

  @include media-screen($screen-md) {
    @include display_flex_mixin(column);
  }

  &__pane {
    width: 250px;
    height: 200px;
    border: 3px solid $main-color;
    border-radius: 20px;
    overflow: hidden;

    @include media-screen($screen-md) {
      margin: auto;
    }
  }

  &__image {
    width: 250px;
    height: 200px;
  }

  &__content {
    width: 100%;
    height: 100%;
  }

  &__header {
    height: 30px;
  }

  &__title {
    @include media-screen($screen-md) {
      font-size: $font-lg;
    }
  }
  
  &__body {
    @include media-screen($screen-md) {
      font-size: $font-sm;
    }
  }

  &__separator {
    grid-column: 1 / 3;
    height: 1px;
    width: 100%;
    background: linear-gradient(
      90deg,
      transparent,
      $main-color, $main-color,
      $main-color, $main-color,
      transparent
    );
  }
}