.page-mini-factories-product {
  &__header {
    margin: 30px;
  }

  &__nav {
    height: 60px;
    width: 100%;
  }

  &__title {
    @include media-screen($screen-md) {
      font-size: $font-title-sm;
    }
  }

  &__body {
    @include wrapper(80%);

    @include media-screen(sm) {
      @include wrapper(90%);
    }

    @include media-screen($screen-sm) {
      @include wrapper(95%);
    }
  }

  @import "../other/article/article";
}