.page-products-21-age-article {
  @include display_flex_mixin(column);

  &__nav {
    height: 60px;
    width: 100%;

    @include media-screen($screen-lg) {
      height: 0;
    }
  }

  &__title {
    margin: 30px 0;
  }

  &__body {
    @include wrapper($screen-md);
    margin: auto;

    @include media-screen(xl) {
      @include wrapper(50%);
    }

    @include media-screen($screen-xl) {
      @include wrapper(70%);
    }

    @include media-screen(sm) {
      @include wrapper(90%);
    }

    @include media-screen($screen-sm) {
      @include wrapper(95%);
    }
  }

  &__pane {
    @include display_flex_mixin(row, center, center);
    overflow: hidden;
    border: 3px solid $main-color;
    border-radius: 20px;
    width: 350px;
    height: 250px;
    margin-right: 30px;
    float: left;
  }

  &__image {
    height: 120%;
    display: flex;
    justify-content: center;
  }

  &__content {
    margin-top: 20px;
  }

  &__part {
    margin-bottom: 20px;
  }
  
  @import "../other/article/article";
}