.text {
  &__brand {
    font-family: 'Kobzak';
    font-size: $font-brand-md;
    font-weight: bold;

    @include media-screen($screen-md) {
      font-size: $font-brand-sm;
    }
  }

  &__title {
    font-size: $font-title-md;
    font-weight: bold;
    text-transform: uppercase;

    &--sm {
      font-size: $font-title-sm;
    }

    @include media-screen($screen-md) {
      font-size: $font-title-sm;
    }
  }

  &__plain {
    font-size: $font-md;

    &--bold {
      font-weight: bold;
    }

    &--center {
      text-align: center;
    }

    &--italic {
      font-style: italic;
    }

    &--small {
      font-size: $font-sm;
    }

    &--smaller {
      font-size: $font-xs;
    }
  }

  &--xs {
    font-size: $font-xs;
  }

  &--sm {
    font-size: $font-sm;
  }

  &--md {
    font-size: $font-md;
  }

  &--lg {
    font-size: $font-lg;
  }

  &--xl {
    font-size: $font-xl;
  }

  &--bold {
    font-weight: bold;
  }

  &--upper {
    text-transform: uppercase;
  }

  &--italic {
    font-style: italic;
  }

  &--center {
    text-align: center;
  }

  &--justify {
    text-align: justify;
  }

  &--green {
    color: $main-color;
  }

  &--dark-green {
    color: $main-color-dark;
  }
}