.work-plan {
  margin-top: 100px;

  &__steps-work {
    @include wrapper(90%);
  }

  &__footer {
    @include wrapper(80%);
  }

  @include media-screen(xl) {
    @include wrapper($screen-lg);
  }

  @include media-screen(lg) {
    @include wrapper($screen-lg);
  }

  @import "steps-work/steps-work";
}