.mini-factories {

  &__item {
    @include display_flex_mixin(row, space-evenly, center);
    margin: 50px 0;

    &:nth-child(even) {
      @include display_flex_mixin(row-reverse, space-evenly, center);

      @include media-screen($screen-md) {
        @include display_flex_mixin(column);
      }
    }

    @include media-screen($screen-md) {
      @include display_flex_mixin(column);
    }
  }

  &__pane {
    width: 49%;
    height: 340px;
    overflow: hidden;
    @include display_flex_mixin(column, center, center);
    justify-items: stretch;
    align-items: stretch;
    position: relative;

    @include media-screen($screen-lg) {
      @include wrapper(95%);
      height: 100%;
    }
  }

  &__image {
    width: 100%;
    flex: 1;
    @include display_flex_mixin(row, center, center);
  }

  &__label {
    position: absolute;
    padding: 10px 30px;
    bottom: 50px;
    width: 80%;
    left: 10%;
    right: 10%;
    background: linear-gradient(
      90deg,
      transparent,
      #fff, #fff, #fff,
      transparent
    );
  }

  &__title {
    @include wrapper(100%);
    flex: 1;
  }

  &__description {
    width: 100%;
    flex: 1;
    color: $main-color-dark;
  }

  &__links{
    @include display_flex_mixin(column, center, center);
    width: 100%;
    text-align: center;
    flex: 2;
  }

  &__link {
    width: 100%;
    margin: 5px;
    border: $home-border;
    border-radius: 10px;
    padding: 5px 10px;
    color: $main-color-dark;

    &:hover {
      background-color: $main-color;
      color: white;
      transition: 0.5s;
    }
  }

  @include media-screen($screen-lg) {
    @include display_flex_mixin(column);
  }
}