.separator {
  &__thin {
    width: 100%;
    height: 1px;
    background: $main-color;
  }

  &__simple {
    width: 100%;
    height: 2px;
    background: $main-color;
  }

  &__bold {
    width: 100%;
    height: 75px;
    padding: 10px;
    display: flex;
    align-items: flex-end;
    background: $main-color;
    color: #fff;

    * {
      color: #fff;
    }
  }
}