.carousel {
  width: $carousel-item-width-with-margin * 3;
  @include display_flex_mixin(row, start, center);
  overflow: hidden;
  position: relative;
  z-index: 1;

  &__item {
    flex: 0 0 auto;
    width: $carousel-item-width;
    height: $carousel-item-width - 100px;
    background: #333;
    margin: 0 10px;
    @include display_flex_mixin(row, center, center);
    z-index: 5;
    transition: 0.5s;
    font-size: 2rem;
    position: relative;

    @include media-screen($screen-sm) {
      width: 280px;
      height: 200px;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
  }

  &__title {
    position: absolute;
    bottom: 40px;
    width: 80%;
    color: $main-color-dark;
    background: linear-gradient(
      90deg,
      transparent,
      #fff, #fff,
      transparent
    );
    padding: 10px 30px;
  }

  @include media-screen(md) {
    width: $carousel-item-width-with-margin * 2;
  }

  @include media-screen($screen-md) {
    width: $carousel-item-width-with-margin;
  }

  @include media-screen($screen-sm) {
    width: 300px;
  }

  @import "btn/btn";
}