.steps-work {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
    margin: 20px 0;
  }

  &__pane {
    width: 100px;
    height: 100px;
    border: $home-border;
    border-width: $home-border-width + 1px;
    border-radius: 50%;
    @include display_flex_mixin(row, center, center);
    overflow: hidden;
  }

  &__image {
    width: 80%;
    height: 80%;
  }

  &__content {
    margin: 10px 0;
    color: $main-color-dark;
  }

  @include media-screen(sm) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include media-screen($screen-sm) {
    grid-template-columns: 1fr;
  }
}