.items-catalog {
  @include display_grid_mixin(1fr, 1fr, 50px);
  height: 100%;
  width: 100%;

  @include media-screen($screen-md) {
    @include display_grid_mixin(1fr, 1fr, 30px);
  }

  &__item {
    @include display_grid_mixin(1fr auto, 1fr, 20px);

    @include media-screen($screen-md) {
      @include display_grid_mixin(1fr, 1fr auto, 30px);
    }
  }

  &__pane {
    overflow: hidden;
    width: 250px;
    height: 200px;
    border-radius: 10px;
    @include display_flex_mixin(row, center, center);

    @include media-screen($screen-lg) {
      width: 200px;
      height: 150px;
    }

    @include media-screen($screen-md) {
      width: 100%;
    }
  }

  &__body {
    @include display_grid_mixin(100%, 1fr, 0);
    height: 100%;
    position: relative;
  }

  &__image {
    height: 100%;

    @include media-screen($screen-lg) {
      width: 200px;
      height: 150px;
      border-radius: 20px;
    }
  }

  &__title {
    position: absolute;
    top: 0;
    padding-left: 10px;
    width: 100%;

    @include media-screen($screen-md) {
      font-size: $font-md;
      padding-left: 50px;
    }
  }

  &__description {
    padding: 30px 0;

    @include media-screen($screen-md) {
      font-size: $font-sm;
      padding: 30px;
    }
  }

  &__btn {
    position: absolute;
    bottom: 0;
    display: none;
    align-items: center;
    padding-left: 10px;
    width: 100%;
    height: 50px;
    background: linear-gradient(
      transparent, #fff, #fff, #fff, #fff
    );
  }
}